import React, { useState } from 'react';
import './BrowserWindow.css';

interface BrowserWindowProps {
  currentSection: string;
  children: React.ReactNode;
  onNavigate: (action: string) => void;
  onClose?: () => void;
  onOpen?: () => void;
  isMobile?: boolean;
  browserOpen?: boolean;
}

// SVG Icon Components
const BackIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <path d="M10 3L5 8l5 5V3z" />
    <rect x="3" y="7" width="8" height="2" />
  </svg>
);

const ForwardIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <path d="M6 3l5 5-5 5V3z" />
    <rect x="5" y="7" width="8" height="2" />
  </svg>
);

const RefreshIcon = () => (
  <svg
    viewBox="0 0 16 16"
    width="14"
    height="14"
    fill="none"
    stroke="currentColor"
    strokeWidth="1.5"
  >
    <path
      d="M14 8a6 6 0 0 1-6 6 6 6 0 0 1-6-6 6 6 0 0 1 6-6 6 6 0 0 1 5.5 3.5"
      strokeLinecap="round"
    />
    <polyline points="11 2 14 2 14 5" />
  </svg>
);

const StopIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <rect x="4" y="4" width="8" height="8" />
  </svg>
);

const HomeIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <path d="M2 8l6-5 6 5v5a1 1 0 0 1-1 1H9v-4H7v4H3a1 1 0 0 1-1-1V8z" />
    <polygon points="8,1 1,7 2,8 8,3 14,8 15,7" />
  </svg>
);

const BrowserWindow: React.FC<BrowserWindowProps> = ({
  currentSection,
  children,
  onNavigate,
  onClose,
  onOpen,
  isMobile = false,
  browserOpen = true,
}) => {
  const [isMaximized, setIsMaximized] = useState(false);

  const getSectionUrl = (section: string): string => {
    const urls: { [key: string]: string } = {
      '': 'http://maw.dev/index.html',
      'goTo-about': 'http://maw.dev/about-me.htm',
      'goTo-work': 'http://maw.dev/work/tech-stack.html',
      'goTo-portfolio': 'http://maw.dev/portfolio/gallery.cgi',
      'goTo-contact': 'http://maw.dev/contact/guestbook.php',
    };
    return urls[section] || 'http://maw.dev/';
  };

  const getSectionTitle = (section: string): string => {
    const titles: { [key: string]: string } = {
      '': 'Welcome to Maw.dev - Web Explorer',
      'goTo-about': 'About Me - Web Explorer',
      'goTo-work': 'Tech Stack - Web Explorer',
      'goTo-portfolio': 'Portfolio Gallery - Web Explorer',
      'goTo-contact': 'Contact Form - Web Explorer',
    };
    return titles[section] || 'Maw.dev - Web Explorer';
  };

  const handleBack = () => {
    onNavigate('');
  };

  const handleHome = () => {
    onNavigate('');
  };

  return (
    <div className={`browser-window ${isMaximized ? 'maximized' : ''}`}>
      {/* Title Bar */}
      <div className="browser-titlebar">
        <div className="browser-titlebar-text">
          <span className="browser-icon"></span>
          {getSectionTitle(currentSection)}
        </div>
        <div className="browser-titlebar-controls">
          <button
            className="titlebar-button minimize"
            aria-label="Minimize"
            onClick={() => {
              if (!isMobile) return; // Desktop: do nothing
              if (isMaximized) {
                setIsMaximized(false);
                onClose?.(); // Move to bottom
              } else if (browserOpen) {
                onClose?.(); // Move to bottom
              }
            }}
          >
            _
          </button>
          <button
            className="titlebar-button maximize"
            aria-label="Maximize"
            onClick={() => {
              if (isMobile && !browserOpen) {
                // Open from bottom first on mobile
                onOpen?.();
                setTimeout(() => setIsMaximized(true), 300);
              } else if (isMobile && isMaximized) {
                // On mobile, if maximized, restore to open state
                setIsMaximized(false);
              } else {
                // Toggle maximize
                setIsMaximized(!isMaximized);
              }
            }}
          >
            
          </button>
          <button
            className="titlebar-button close"
            aria-label="Close"
            onClick={() => {
              if (!isMobile && !isMaximized) return; // Desktop regular: do nothing
              if (isMobile) {
                setIsMaximized(false);
                onClose?.(); // Always move to bottom on mobile
              }
            }}
          >
            
          </button>
        </div>
      </div>

      {/* Menu Bar */}
      <div className="browser-menubar">
        <span className="menu-item">File</span>
        <span className="menu-item">Edit</span>
        <span className="menu-item">View</span>
        <span className="menu-item">Favorites</span>
        <span className="menu-item">Tools</span>
        <span className="menu-item">Help</span>
      </div>

      {/* Navigation Bar */}
      <div className="browser-navbar">
        <div className="nav-buttons">
          <button
            className="nav-button back"
            onClick={handleBack}
            disabled={currentSection === ''}
            aria-label="Back"
          >
            <BackIcon />
          </button>
          <button className="nav-button forward" disabled aria-label="Forward">
            <ForwardIcon />
          </button>
          <button className="nav-button refresh" aria-label="Refresh">
            <RefreshIcon />
          </button>
          <button className="nav-button stop" aria-label="Stop">
            <StopIcon />
          </button>
          <button className="nav-button home" onClick={handleHome} aria-label="Home">
            <HomeIcon />
          </button>
        </div>
        <div className="nav-addressbar">
          <input
            type="text"
            value={getSectionUrl(currentSection)}
            readOnly
            className="addressbar-input"
          />
        </div>
        <button className="nav-button go">Go</button>
      </div>

      {/* Content Area */}
      <div className="browser-content">
        <div className="browser-viewport">{children}</div>
      </div>

      {/* Status Bar */}
      <div className="browser-statusbar">
        <div className="status-text">Done</div>
        <div className="status-icons">
          <span className="status-icon"></span>
          <span className="status-zone">Internet</span>
        </div>
      </div>
    </div>
  );
};

export default BrowserWindow;
