'use client';
import { useRef, useState } from 'react';
import { GameCard } from './ui/GameCard';
import { GameOverlay } from './ui/GameOverlay';
import RacingGame from './racing/game';

export default function GameSelector({ visitor, setDialogText, setCurrentAction }) {
  const [selectedGame, setSelectedGame] = useState('');
  const [pointsEarned, setPointsEarned] = useState<number>(0);
  const [gameOver, setGameOver] = useState<boolean>(false);

  const gameRef = useRef<any>(null);

  if (gameOver) {
    return (
      <GameOverlay
        type="custom"
        title={pointsEarned > 0 ? ' Felicidades!' : ' Lo sentimos!'}
        message={pointsEarned > 0 ? `Ganaste ${pointsEarned} puntos` : 'Intenta de nuevo'}
        brandColor="#00d26a"
        onAction={() => {
          setGameOver(false);
          setPointsEarned(0);
          setSelectedGame('');
        }}
        actionText="Volver al Men de Juegos"
      />
    );
  }

  if (selectedGame !== '') {
    return (
      <>
        <div className="game-wrapper" ref={gameRef}>
          {selectedGame === 'races' && (
            <RacingGame
              laps={1}
              track={1}
              setGameOver={setGameOver}
              setPointsEarned={setPointsEarned}
              showBillboardStats={false}
              brandColor="#ef4444"
              gameTitle="MAW Racing"
              // skin="lapi"
            />
          )}
        </div>
      </>
    );
  }

  return (
    <div className="game-selector-container">
      <div className="game-selector-header">
        <h1 className="game-selector-title"> Juegos</h1>
        <p className="game-selector-subtitle">Selecciona un juego para comenzar a practicar</p>
      </div>

      <div className="game-selector-grid">
        <GameCard
          title="Carreras"
          icon=""
          gradientFrom="#3b82f6"
          gradientTo="#2563eb"
          onClick={() => setSelectedGame('races')}
          description="Velocidad 3D"
        />
      </div>
    </div>
  );
}
